import api from '../../common/api';
export default {
	data() {
		return {
			account:'lxt',
			password:'123456'
		};
	},
	onLoad() {
		// uni.switchTab({
		// 	url: '/pages/home/my'
		// });
		// uni.navigateTo({
		// 	url:'/pages/home/echo'
		// })
	},
	onShow(){
	},
	mounted(){
		
	},
	methods: {
		login(){
			this.$btnAudioContext.play();
			let params={
				username:this.account,
				password:this.password
			}
			api.login(params).then(res => {
				uni.showToast({
					title: '登录成功',
					duration: 2000
				});
				uni.setStorage({
					key: 'storageData',
					data: res,
					success: function (res) {
						uni.switchTab({
							url: '/pages/home/my'
						});
					}
				});
			})
		}
	}
}
